*{
    margin: 0;
    padding: 0;
}
ul{
    padding: 0;
    margin: 0;
}
li{
    list-style: none;
}

body{
    background-color: #16171c;
    font-family: 'Open Sans', sans-serif;
}

/* #region Dashboard */
.Dashboard {
  position: absolute;
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
  padding: 20px;
}

.Dashboard .row {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-bottom: 10px;
}

.Dashboard .left-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #1f2025;
  width: 33%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  border-radius: 15px;
  margin-left: 150px;
  margin-right: 150px;
}

.Dashboard .center {
  display: flex;
  margin-left: 50px;
  margin-right: 50px;
  background: #1f2025;
  justify-content: center;
  align-items: center;
  padding: 20px;
  margin-top: 40px;
  border-radius: 15px;
}

.Dashboard .right-side {
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #1f2025;
  width: 33%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  border-radius: 15px;
  margin-left: 150px;
  margin-right: 150px;
}

.Dashboard .inputbar-weight {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.Dashboard .inputbar-calories {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.Dashboard .inputfield {
  padding: 10px;
  border-radius: 15px;
  background-color: #2a2a2a;
  border: none;
  outline: none;
  font-size: 16px;
  margin-right: 5px;
  text-align: center;
  color: #b3b3b3;
}

.Dashboard .weight-change-container{
  text-align: center;
}

.Dashboard .weight-change{
  padding: 10px;
}

.Dashboard .weight-increase{
  color: green;
}

.Dashboard .weight-decrease{
  color: red;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  appearance: textfield;
}


.Dashboard .addweightbutton{
  background-color: #1d89ef;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

.Dashboard .clear-data-button{
  background-color: #1d89ef;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

.Dashboard .clear-data-button:focus,
.Dashboard .databuttons:focus,
.Dashboard .addweightbutton:focus{
  outline: none;
  box-shadow: 0 0 0 3px rgba(29, 137, 239, 0.5);
}

.Dashboard .databuttons{
  background-color: #1d89ef;
  margin: 5px;
  color: #fff;
  padding: 10px 20px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

.Dashboard .databuttons:hover,
.Dashboard .addweightbutton:hover,
.Dashboard .clear-data-button:hover{
    background-color: #196ebd;
}


/* #endregion Dashboard */

/* #region ProgressTracker */

.progresstracker {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.progresstracker .targetheader,
.progresstracker .targetmin {
  width: 100%;
  text-align: center;
  color: #b3b3b3;
}

.progresstracker .inputtargetweight,
.progresstracker .inputmin {
  width: 10%;
  padding: 10px;
  border-radius: 15px;
  background-color: #2a2a2a;
  border: none;
  outline: none;
  font-size: 16px;
  margin: 5px auto;
  text-align: center;
  color: #b3b3b3;
}

/* #endregion ProgressTracker */

/* #region WeightTable */

.weighttable-container {
    position: relative;
    height: calc(105vh - 200px);
    overflow-y: auto;
  }
  
  .weighttable {
    width: 100%;
    color: #b3b3b3;
  }
  
  .weighttable .table {
    width: 100%;
    text-align: left;
  }
  
  
  .weighttable .savebutton,
  .weighttable .cancelbutton,
  .weighttable .editbutton {
    display: inline-block;
    padding: 6px 12px;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
  }
  
  .weighttable .savebutton {
    background-color: #1d89ef;
    color: #fff;
    border: none;
  }
  
  .weighttable .cancelbutton {
    background-color: #9a9a9a;
    color: #fff;
    border: none;
    margin-left: 5px;
  }
  
  .weighttable .editbutton {
    background-color: #1d89ef;
    color: #fff;
    border: none;
    margin-left: 25px;
    margin-right: 25px;
  }
  
  .weighttable .editbutton:hover {
    background-color: #bdbdbd;
  }

  .weighttable .inputfield-weight{
    background-color: #2a2a2a;
    outline: none;
    border: none;
    border-radius: 15px;
    padding: 5px;
    color: #b3b3b3;
  }
  
  .weighttable .table th {
    padding: 8px;
    width: 100%;
    background-color: #1f2025;
    color: #b3b3b3;
  }
  
  .weighttable .table td {
    padding: 8px;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .weighttable .table tr:nth-child(even) {
    background-color: #1f2025;
  }
  
  .weighttable .table thead {
    position: sticky;
    top: 0;
    z-index: 1;
  }
  
  .weighttable .table tbody {
    overflow-y: auto;
    max-height: 100%;
  }

/* #endregion WeightTable */

/* #region CalorieTable */

.calorietable-container {
  position: relative;
  height: calc(105vh - 200px);
  overflow-y: auto;
}

.calorietable {
  width: 100%;
  color: #b3b3b3;
}

.calorietable .table {
  width: 100%;
  text-align: left;
}


.calorietable .savebutton,
.calorietable .cancelbutton,
.calorietable .editbutton {
  display: inline-block;
  padding: 6px 12px;
  border-radius: 5px;
  font-size: 14px;
  cursor: pointer;
}

.calorietable .savebutton {
  background-color: #1d89ef;
  color: #fff;
  border: none;
}

.calorietable .cancelbutton {
  background-color: #9a9a9a;
  color: #fff;
  border: none;
  margin-left: 5px;
}

.calorietable .editbutton {
  background-color: #1d89ef;
  color: #fff;
  border: none;
  margin-left: 25px;
  margin-right: 25px;
}

.calorietable .editbutton:hover {
  background-color: #bdbdbd;
}

.calorietable .table th {
  padding: 8px;
  width: 100%;
  background-color: #1f2025;
  color: #b3b3b3;
}

.calorietable .table td {
  padding: 8px;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.calorietable .table tr:nth-child(even) {
  background-color: #1f2025;
}

.calorietable .table thead {
  position: sticky;
  top: 0;
  z-index: 1;
}

.calorietable .inputfield-calories{
  background-color: #2a2a2a;
  outline: none;
  border: none;
  border-radius: 15px;
  padding: 5px;
  color: #b3b3b3;
}

.calorietable .table tbody {
  overflow-y: auto;
  max-height: 100%;
}

.calorietable .table tbody::-webkit-scrollbar {
  width: 8px;
}

.calorietable .table tbody::-webkit-scrollbar-thumb {
  background-color: #6b6b6b;
  border-radius: 4px;
}

.calorietable .table tbody::-webkit-scrollbar-thumb:hover {
  background-color: #555555;
}

.calorietable .table tbody::-webkit-scrollbar-track {
  background-color: #2a2a2a;
}

.calorietable .table tr:last-child {
  margin-bottom: 10px;
}

/* #endregion CalorieTable */

/* #region SideMenu */
.side-menu{
    position: absolute;
    background: #1f2025;
    width: 250px;
    height: 100%;
    box-sizing: border-box;
    padding: 30px 20px;
}

.side-menu .top-section{
    position: relative;
}

.side-menu .top-section .logo{
    width: 50px;
    height: 50px;
    overflow: hidden;
    display: inline-block;
}
.side-menu .top-section .logo img{
    max-width: 100%;
    max-height: 100%;
}

.side-menu .main-menu .menu-item{
    color: #b3b3b3;
    text-decoration: none;
    font-size: 15px;
    display: block;
    font-weight: 600;
    cursor: pointer;
}

.side-menu .main-menu .menu-item:hover{
    background-color: #282828;
    border-radius: 15px;
}

.side-menu .main-menu .menu-item.active{
    background: #1d89ef;
    border-radius: 15px;
    color: #fff;
}


.side-menu .main-menu{
    margin: 5px 0;
    position: relative;
    max-height: 250px;
    overflow-y: hidden;
    overflow-x: hidden;
}

.side-menu .main-menu .menu-item .menu-icon{
    display: inline-block;
    width: 40px;
    height: 40px;
    font-size: 20px;
    line-height: 40px;
    text-align: center;
}

.side-menu .main-menu .menu-item span{
    position: absolute;
    display: inline-block;
    line-height: 40px;
    opacity: 1;
    transition: opacity .2s ease-in;
}
/* #endregion SideMenu */

/* #region Footer */
.side-menu-footer{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0%;
    padding: 20px;
}

.side-menu-footer .avatar{
    width: 60px;
    height: 60px;
    overflow: hidden;
}
.side-menu-footer .avatar img{
    max-width: 100%;
}
/* #endregion Footer */

.divider{
    width: 100%;
    height: 1px;
    border-radius: 5px;
    background: #9a9a9a;
}

.container{
    margin-left: 200px;
    padding: 50px;
    box-sizing: border-box;
}

.container.inactive{
    margin-left: 80px;
}